<template>
  <div class="post-wrapper" :class="{ 'anime-up': postUp, 'anime-start': postStart }">
    <ScrollMarker @intersect="backInUp" />
    <Form v-if="simplePost.form" />
    <div class="post-header" v-if="!simplePost.form && !simplePost.advertising">
      <div class="post-header-ava">
        <img :src="require('@/assets/images/ava/astrid.svg')" alt="">
      </div>
      <div class="user-box">
        <div class="user-box-name">{{ simplePost.name }}</div>
        <div class="user-box-time-ago">
          {{ simplePost.hour }} hours ago
        </div>
      </div>
      <div class="post-header-bell" @click="toggleBell">
        <svg width="14" height="16" viewBox="0 0 14 16" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path :class="{ 'bell-active': bellActive }" d="M9.25 13.25C9.25 13.8468 9.01295 14.4191 8.59099 14.841C8.16903 15.263 7.59674 15.5 7 15.5C6.40326 15.5 5.83097 15.263 5.40901 14.841C4.98705 14.4191 4.75 13.8468 4.75 13.25H1C0.801088 13.25 0.610322 13.171 0.46967 13.0304C0.329018 12.8897 0.25 12.6989 0.25 12.5C0.25 12.3011 0.329018 12.1103 0.46967 11.9697C0.610322 11.829 0.801088 11.75 1 11.75H1.75V7.25002C1.74915 6.25294 2.03224 5.27624 2.56617 4.43417C3.1001 3.5921 3.86279 2.91949 4.765 2.49502C4.82861 1.94688 5.09144 1.44122 5.50352 1.0742C5.9156 0.707183 6.44818 0.504395 7 0.504395C7.55182 0.504395 8.0844 0.707183 8.49648 1.0742C8.90856 1.44122 9.17139 1.94688 9.235 2.49502C10.1372 2.91949 10.8999 3.5921 11.4338 4.43417C11.9678 5.27624 12.2509 6.25294 12.25 7.25002V11.75H13C13.1989 11.75 13.3897 11.829 13.5303 11.9697C13.671 12.1103 13.75 12.3011 13.75 12.5C13.75 12.6989 13.671 12.8897 13.5303 13.0304C13.3897 13.171 13.1989 13.25 13 13.25H9.25ZM6.25 13.25C6.25 13.4489 6.32902 13.6397 6.46967 13.7804C6.61032 13.921 6.80109 14 7 14C7.19891 14 7.38968 13.921 7.53033 13.7804C7.67098 13.6397 7.75 13.4489 7.75 13.25H6.25ZM6.25 3.57502C5.40298 3.74792 4.6417 4.20816 4.09502 4.87784C3.54834 5.54753 3.24982 6.38553 3.25 7.25002V11.75H10.75V7.25002C10.7502 6.38553 10.4517 5.54753 9.90498 4.87784C9.3583 4.20816 8.59702 3.74792 7.75 3.57502V2.75002C7.75 2.55111 7.67098 2.36034 7.53033 2.21969C7.38968 2.07904 7.19891 2.00002 7 2.00002C6.80109 2.00002 6.61032 2.07904 6.46967 2.21969C6.32902 2.36034 6.25 2.55111 6.25 2.75002V3.57502Z" fill="#A5B4CB"/>
        </svg>
      </div>
      <div class="post-header-plus">
        <svg width="34" height="34" viewBox="0 0 34 34" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="1" y="1" width="32" height="32" rx="9" stroke="#E4EAF3" stroke-width="2" stroke-dasharray="16 10"/>
          <path d="M21.0568 16.3237H17.6761V12.943C17.6761 12.5698 17.3732 12.2668 17 12.2668C16.6268 12.2668 16.3238 12.5698 16.3238 12.943V16.3237H12.9432C12.5699 16.3237 12.267 16.6266 12.267 16.9998C12.267 17.373 12.5699 17.6759 12.9432 17.6759H16.3238V21.0566C16.3238 21.4298 16.6268 21.7328 17 21.7328C17.3732 21.7328 17.6761 21.4298 17.6761 21.0566V17.6759H21.0568C21.43 17.6759 21.7329 17.373 21.7329 16.9998C21.7329 16.6266 21.43 16.3237 21.0568 16.3237Z" fill="#A5B4CB"/>
        </svg>
      </div>
    </div>
    <div class="post-content" v-if="!simplePost.form">
      <PostText v-if="simplePost.short" :shortText="simplePost"/>
      <PostLongText v-if="simplePost.long" :longText="simplePost"/>
      <PostVideo v-if="simplePost.video" :video="simplePost"/>
      <PostPhotoPlus v-if="simplePost.photoPlus" :photoPlus="simplePost"/>
      <PostAudio v-if="simplePost.audio" :postAudio="simplePost"/>
      <PostSoundCloud v-if="simplePost.soundCloud" :postSound="simplePost"/>
      <PostGallery v-if="simplePost.gallery" :postGallery="simplePost"/>
      <PostGoogleMap v-if="simplePost.googleMap" :postMap="simplePost"/>
      <PostGive v-if="simplePost.give" :postGive="simplePost"/>
      <PostConference v-if="simplePost.conference" :postConference="simplePost"/>
      <PostNested v-if="simplePost.nested" :postNested="simplePost"/>
      <Advertising v-if="simplePost.advertising" :advertising="simplePost"/>
      <PostBuyAccess v-if="simplePost.buy" :buyAccess="simplePost"/>
      <PostShow v-if="simplePost.showMe" :show="simplePost"/>
      <PostVote v-if="simplePost.vote" :postVote="simplePost"/>
      <div class="post-content-tegs" v-if="!simplePost.advertising">
        <div class="post-content-tegs-html">#html</div>
        <div class="post-content-tegs-css">#css</div>
      </div>
    </div>
    <div class="post-footer" v-if="!simplePost.form && !simplePost.advertising">
      <div class="post-footer-views">
        <img :src="require('@/assets/images/svg/views.svg')" alt="">
        <div class="post-footer-views-num">{{ simplePost.views }}</div>
      </div>
      <div class="post-footer-other">
        <div class="post-footer-other-item" ref="acorn">
          <img :src="require('@/assets/images/svg/acorn2.svg')" alt="">
          <div class="post-footer-other-item-num">{{ simplePost.likes }}</div>
          <div class="post-footer-popup" ref="popup">
            <div class="post-footer-popup-bottom">
              <img :src="require('@/assets/images/svg/acorn2.svg')" alt="">
              <div class="post-footer-other-item-num">{{ simplePost.likes }}</div>
            </div>
          </div>
        </div>
        <div class="post-footer-other-item">
          <img :src="require('@/assets/images/svg/share.svg')" alt="">
          <div class="post-footer-other-item-num">{{ simplePost.share }}</div>
        </div>
        <div class="post-footer-other-item">
          <img :src="require('@/assets/images/svg/comments.svg')" alt="">
          <div class="post-footer-other-item-num">{{ simplePost.comments }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostText from '@/components/PostText.vue'
import PostLongText from '@/components/PostLongText.vue'
import PostVideo from '@/components/PostVideo.vue'
import PostPhotoPlus from '@/components/PostPhotoPlus.vue'
import Form from '@/components/Form.vue'
import PostAudio from '@/components/PostAudio.vue'
import PostSoundCloud from '@/components/PostSoundCloud.vue'
import PostGallery from '@/components/PostGallery.vue'
import PostGoogleMap from '@/components/PostGoogleMap.vue'
import PostGive from '@/components/PostGive.vue'
import PostConference from '@/components/PostConference.vue'
import PostNested from '@/components/PostNested.vue'
import Advertising from '@/components/Advertising.vue'
import PostBuyAccess from '@/components/PostBuyAccess.vue'
import PostShow from '@/components/PostShow.vue'
import PostVote from '@/components/PostVote.vue'
import ScrollMarker from '@/components/ScrollMarker.vue'

export default {
  name: 'SimplePost',
  props: ['simplePost'],
  components: {
    PostText,
    PostLongText,
    PostVideo,
    PostPhotoPlus,
    Form,
    PostAudio,
    PostSoundCloud,
    PostGallery,
    PostGoogleMap,
    PostGive,
    PostConference,
    PostNested,
    Advertising,
    PostBuyAccess,
    PostShow,
    PostVote,
    ScrollMarker,
  },
  data() {
    return {
      bellActive: false,
      popupLeft: '',
      postUp: false,
      postStart: true,
      scrollFlag: true,
    }
  },
  methods: {
    toggleBell() {
      this.bellActive = !this.bellActive;
    },
    setLeft() {
      let width = 0;
      if(this.$refs.acorn){
        width = this.$refs.acorn.clientWidth;
        this.$refs.popup.style.left = -(90 - width / 2) + 'px';
      }
    },
    backInUp() {
      if(this.scrollFlag) {
        this.postUp = true;
        setTimeout(() => {
          this.postStart = false;
          this.postUp = false;
          this.scrollFlag = false;
        }, 500);
      }
      // this.postUp = true;
      // setTimeout(() => {
      //   this.postStart = false;
      //   this.postUp = false;
      // }, 500);
    }
  },
  mounted() {
    this.setLeft();
    window.addEventListener('resize', () => this.setLeft());
  }
}
</script>

<style>
.post-wrapper {
  margin: var(--margin-dt) 0;
  flex: 1;
  transition: 1s;
}
.anime-start {
  transform: translateY(100px) scale(0.7);
}
.anime-up {
  transform: translateY(0) scale(1);
}
.post-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
}
.post-header-ava {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  overflow: hidden;
}
.post-header-ava img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.user-box {
  flex: 1;
  margin-left: 18px;
}
.user-box-name {
  font-size: 16px;
  font-weight: 600;
}
.user-box-name:hover {
  color: var(--logo-color);
}
.user-box-time-ago {
  font-size: 12px;
  font-weight: 600;
  color: var(--primary-gray);
}
.post-header-bell {
  width: 18px;
  height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
}
.bell-active {
  fill: var(--logo-color);
}
.post-header-ava:hover,
.user-box-name:hover,
.post-header-plus:hover,
.post-header-bell:hover {
  cursor: pointer;
}
.post-header-plus rect {
  transition: stroke .5s ease;
}
.post-header-plus path {
  transition: fill .5s ease;
}
.post-header-plus:hover rect {
  stroke: var(--logo-color);
}
.post-header-plus:hover path {
  fill: var(--logo-color);
}
.post-content {
  background: #fff;
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0 0 0;
  padding: var(--margin-dt);
}
.post-content-text {
  position: relative;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: calc(var(--margin-dt) - 12px);
}
.post-content-text-over {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: linear-gradient( rgba(255, 255,255, 0) 63%, rgba(255, 255,255, .8) 81%, rgba(255, 255,255, 1) 100% );
}
.post-content-text-over-title {
  text-align: center;
  font-size: 22px;
  font-weight: 500;
}
.post-content-text p {
  line-height: 32px;
  margin-top: -12px;
}
.post-content-tegs {
  display: flex;
  align-items: center;
  margin: 20px var(--margin-dt) 0 0;
}
.post-content-tegs-html,
.post-content-tegs-css {
  font-size: 12px;
  font-weight: 700;
  margin-right: 20px;
}
.post-content-tegs-html:hover,
.post-content-tegs-css:hover {
  cursor: pointer;
}
.post-content-tegs-html {
  color: var(--primary-gray);
}
.post-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.post-footer-views {
  display: flex;
  align-items: center;
}
.post-footer-views-num {
  font-size: 12px;
  font-weight: 700;
  color: var(--primary-gray);
  margin-left: 8px;
}
.post-footer-other {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 5px;
}
.post-footer-other-item {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.post-footer-other-item:first-child {
  height: 50px;
  padding: 0 15px;
}
.post-footer-other-item:hover {
  cursor: pointer;
}
.post-footer-popup {
  display: none;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  bottom: 50%;
  left: -100%;
  width: 180px;
  height: 180px;
  background: #FFFFFF;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  z-index: 11;
}
.post-footer-popup:hover {
  cursor: auto;
}
.post-footer-popup-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  background: var(--logo-color);
  height: 50px;
  border-radius: 25px;
  color: #fff;
  margin-bottom: -25px;
}
.post-footer-popup-bottom:hover {
  cursor: pointer;
}
.post-footer-other-item:first-child:hover .post-footer-popup {
  display: flex;
}
.post-footer-other-item-num {
  font-size: 14px;
  font-weight: 700;
  margin-left: 8px;
}
@media (max-width: 550px) {
  .post-wrapper {
    width: 100%;
  }
}
</style>

