<template>
  <div class="post-give">
    <div class="post-give-top">
      <img :src="postConference.url" alt="">
      <div class="post-give-top-panel">
        <div class="post-give-top-panel-title"> {{ postConference.title }}</div>
      </div>
      <div class="post-give-top-date">
        <div class="post-give-top-month">{{ postConference.month }}</div>
        <div class="post-give-top-day">{{ postConference.day }}</div>
      </div>
    </div>

    <div class="post-give-info">
      <div class="post-give-info-top" style="margin-bottom: 24px">
        <div class="post-give-info-top-time">
          <img :src="require('@/assets/images/svg/clock.svg')" alt="">
          <span>8</span>
          <span>days</span>
          <span>16</span>
          <span>hours</span>
        </div>
      </div>
      <div class="post-content-text">
        <p>{{ postConference.text }}</p>
      </div>
    </div>
    <div class="conf-schedule-box"
      v-for="item in postConference.schedule"
      :key="item.id"
    >
      <div class="conf-schedule-time">
        <div class="conf-schedule-time-circle"></div>
        <div class="conf-schedule-time-line"></div>
        <div class="conf-schedule-time-num">{{ item.start }}</div>
      </div>
      <div class="post-give-info">
        <div class="conf-schedule-title">{{ item.title }}</div>
        <div class="post-content-text">
          <p class="pg">{{ item.text }}</p>
        </div>
        <div class="conf-schedule-item-gallery" v-if="item.images">
          <div class="conf-schedule-item-gallery-item"
            v-for="(url, i) in item.images"
            :key="i"
          >
            <img :src="url" alt="">
          </div>
        </div>
      </div>
    </div>

    <div class="post-give-info">
      <div class="post-give-info-top">
        <div class="post-give-info-top-time">
          <img :src="require('@/assets/images/svg/clock.svg')" alt="">
          <span>Total time</span>
          <span>12</span>
          <span>hours</span>
        </div>
      </div>
      <div class="conf-address">
