        <div class="conf-address-arrow">
          <svg width="18" height="18" xmlns="http://www.w3.org/2000/svg">
            <line x1="7" x2="14" y1="4" y2="9" stroke="#A5B4CB" fill="transparent" stroke-width="2"/>
            <line x1="14" x2="7" y1="9" y2="14" stroke="#A5B4CB" fill="transparent" stroke-width="2"/>
          </svg>
        </div>
      </div>
      <div class="post-give-give mt0">
        <div class="post-give-give-content">
          <span>Buy Ticket</span>
