        </div>
        <div class="post-give-bottom-item">
          <img src="https://kinolift.ru/media/users/21030/611692_l.jpg" alt="">
        </div>
        <div class="post-give-bottom-item">
          <img src="https://st.kp.yandex.net/im/kadr/3/2/6/kinopoisk.ru-Lisa-Solberg-3262924.jpg" alt="">
        </div>
        <div class="post-give-bottom-likes">+79</div>
      </div>
    </div>
    
  </div>
</template>


<script>
export default {
  name: 'PostConference',
  props: ['postConference']
}
</script>

<style>
.post-give-top-date {
  position: absolute;
  bottom: 0;
  left: var(--margin-dt);
  width: 60px;
  height: 60px;
  background: #FFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
}
.post-give-top-month {
  font-size: 14px;
  font-weight: 600;
  color: var(--primary-gray);
  text-align: center;
  margin-top: 8px;
}
.post-give-top-day {
  font-size: 22px;
  font-weight: 600;
  color: var(--logo-color);
  text-align: center;
  margin-top: 2px;
}
.conf-schedule-time {
  display: flex;
  align-items: center;
  background: #fff;
  height: 27px;
  margin-left: -5px;
}
.conf-schedule-time-circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid var(--logo-color);
}
.conf-schedule-time-line {
  width: 8px;
  border-bottom: 1px solid #E8ECF2;
  margin: 0 10px;
}
.conf-schedule-time-num {
  color: var(--logo-color);
  font-weight: 600;
}
.conf-schedule-title {
  font-size: 18px;
  font-weight: 600;
  color: #141736;
  margin-bottom: 24px;
}
.conf-schedule-item-gallery {
  display: flex;
}
.conf-schedule-item-gallery-item {
  width: 100px;
  height: 100px;
  margin-right: 3px;
}
.conf-schedule-item-gallery-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.conf-address {
  display: flex;
  align-items: center;
  height: 50px;
  border: 1px solid #E8ECF2;
  border-radius: 8px;
  margin: var(--margin-dt) 0 12px 0;
  background: #fff;
  transition: .3s;
}
.conf-address:hover {
  cursor: pointer;
  border: 1px solid #000;
}
.conf-address-ballon {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.conf-address-ballon path {
  transition: fill .3s;
}
.conf-address:hover .conf-address-ballon path {
  fill: #000;
}
.conf-address-text {
  flex: 1;
  font-size: 12px;
  font-weight: 600;
  color: var(--primary-gray);
}
.conf-address:hover .conf-address-text {
  color: #000;
}
.conf-address-arrow {
  width: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 18px;
}
.conf-address-arrow line {
  transition: stroke .3s;
}
.conf-address:hover .conf-address-arrow line {
  stroke: #000;
}
.pg {
  color: var(--primary-gray);
}
.mt0 {
  margin-top: 0;
}
</style>