<template>
  <div class="post-buy-wrapper">
    <div class="post-buy">
      <PostVideo v-if="show.showMe" :video="getPost"/>
    </div>
    <div class="post-buy-panel">
        <div class="post-buy-btn">
          <div class="post-buy-btn-pic" style="font-size: 12px;font-weight: 600;">
            18+
          </div>
          <div class="post-buy-btn-title">Show me</div>
        </div>
      </div>
  </div>
</template>

<script>
import PostVideo from '@/components/PostVideo.vue'

export default {
  name: 'PostShow',
  props: ['show'],
  components: {
    PostVideo,
  },
  computed: {
    getPost() {
      // console.log(this.$store.state.posts.filter(el => el.showMe)[0]);
      return this.$store.state.posts.filter(el => el.video)[0];
    }
  }
}
</script>

