import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/styles/styles.css'
import Vue2Touch from 'vue2-touch'

Vue.config.productionTip = false
Vue.use(Vue2Touch);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
