<template>
  <div class="advertising-wrapper" 
    :style="bgImg" 
    @mouseover="setTransform" 
    @mouseleave="clearTransform" 
  >
    <div class="advertising-wrapper-over" :style="userRotate">
      <div class="advertising-img" :style="userMove">
        <img :src="advertising.url2" alt="">
      </div>
      <div class="advertising-title" :style="userMove">{{ advertising.title }}</div>
      <div class="post-content-text" style="text-align:center" :style="userMove">
        <p>{{ advertising.text }}</p>
      </div>
      <div class="advertising-btn" :style="userMove">
        <div class="advertising-btn-title">
          Action
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Advertising',
  props: ['advertising'],
  data() {
    return {
      userMove: 'transform: translateX() translateY();',
      bgImg: `background-image: url(${this.advertising.url});`,
      userRotate : `transform: rotateX(${this.rX}deg) rotateY(${this.rY}deg);`,
      rX: 0,
      rY: 0,
    }
  },
  computed: {
  },
  methods: {
    setTransform(e) {
      let coords = this.$el.getBoundingClientRect();
      // console.log(coords);
      let centerX = coords.x + coords.width / 2;
      let centerY = coords.y + coords.height / 2;
      let x = (e.clientX - centerX) / 20;
      let y = (e.clientY - centerY) / 20;
      this.rX = e.clientX - centerX;
      this.rY = e.clientY - centerY;
      this.userMove = `transform: translateX(${x}px) translateY(${y}px);`;
      this.bgImg += `transform: rotateX(${x}deg) rotateY(${y / 2}deg);`;
    },
    clearTransform() {
      this.userMove = `transform: translateX(0) translateY(0);`;
    }
  },
  mounted() {
    this.$el.parentElement.style.padding = 0;
  }
}
</script>

<style>
.advertising-wrapper {
  width: 100%;
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  transition: .8s;
}
.advertising-wrapper * {
  transition: .8s;
}
.advertising-wrapper:hover {
  cursor: pointer;
}
.advertising-wrapper-over {
  width: 100%;
  height: 100%;
  background: rgba(63, 90, 255, 0.8);
  border-radius: 10px;
  padding: var(--margin-dt);
}
.advertising-img {
  margin-bottom: var(--margin-dt);
}
.advertising-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}
.advertising-title {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 10px;
}
.advertising-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  border-radius: 8px;
  background: #fff;
  margin-top: var(--margin-dt);
  transition: .3s;
}
.advertising-btn-title {
  font-size: 16px;
  font-weight: 700;
  color: var(--logo-color);
}
.advertising-btn:hover {
  cursor: pointer;
  background: #ededed;
}
.advertising-btn:hover .advertising-btn-title {
  color: #000;
}
</style>