<template>
	<div class="select-wrapper" @mouseleave="showList = false">
		<div class="select-header" 
			:class="{ 'form-select': selectData.formSelect, 'b-rad-0': showList }" 
			@click.stop="showList = !showList" 
		>
			<div class="select-header-left" v-if="selectData.image">
				<img :src="require('@/assets/images/svg/' + selectData.image)" alt="" />
			</div>
			<div class="select-header-middle">
				<div class="select-header-middle-top" 
				:class="{ 'color-white': selectData.colorW, 'pl': selectData.formSelect }">
					{{ selectData.section }}
				</div>
				<div class="select-header-middle-bottom" 
					:class="{ 'color-white': selectData.colorW , 'pl': selectData.formSelect }">
					{{ title || selectData.list[0] }}
				</div>
			</div>
			<div class="select-header-flag" v-if="selectData.flags">
				<img :src="require('@/assets/images/svg/' + flagCountry)" alt="" />
			</div>
			<div class="select-header-right" :class="{'arrow-up': showList}">
				<img :src="require('@/assets/images/svg/' + selectData.arrow)" alt="" />
			</div>
		</div>
		<transition name="fade">
			<div class="select-list" v-if="showList" :class="{ 'select-list-form': isForm }">
				<div
					class="select-list-item"
					v-for="(elem, index) in selectData.list"
					:key="index"
          @click="getSelect(elem, index, selectData.flags || null)" 
					:class="{ 'select-list-item-form': isForm }"
				>
					<div class="header-tags-search-item-name">{{ elem }}</div>
          <div class="header-tags-search-item-num">{{ ++index * 10000 }}</div>
					<!-- {{ elem }} -->
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
export default {
	name: "Select",
	props: ["selectData", "hover"],
	data() {
		return {
			showList: false,
      title: '',
      flagCountry: 'us.svg',
			isForm: this.hover || false,
		};
	},
  methods: {
    getSelect(name, i, arr) {
      this.title = name;
      this.showList = false;
      if(arr) {
        this.flagCountry = arr[i];
      }
    }
  }
};
</script>

<style>
.select-wrapper {
	position: relative;
	width: 100%;
	flex: 1;
	height: 60px;
	min-height: 60px;
	margin-right: var(--margin-dt);
	/* background: #fff; */
}
.select-wrapper:last-child {
	margin-right: 0;
}
.select-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: #fff;
	border-radius: 10px;
  height: 97%;
	box-shadow: 0px 20px 40px rgba(15, 14, 69, 0.03);
}
.b-rad-0 {
	border-bottom-right-radius: 0;
	border-bottom-left-radius: 0;
}
.select-header-left {
	margin: 0 10px 0 20px;
}
.select-header-left:hover {
	cursor: pointer;
}
.select-header-middle {
	flex: 1;
	margin: 10px;
}
.select-header-middle-top {
	font-size: 10px;
	color: #a5b4cb;
  margin-bottom: 5px;
}
.select-header-middle-bottom {
	font-size: 14px;
	font-weight: 600;
	color: #000;
}
.select-header-flag {
	width: 16px;
	height: 16px;
  border: 1px solid var(--main-bg-color);
	border-radius: 50%;
	overflow: hidden;
	margin-right: 20px;
}
.select-header-flag img {
	width: 100%;
	height: 100%;
	object-fit: contain;
}
.select-header-right {
	width: 18px;
	height: 18px;
	margin-right: 20px;
}
.select-header:hover {
	cursor: pointer;
}
.select-list {
	position: absolute;
	top: 97%;
	left: 0;
	width: 100%;
	background: #f5f5f5;
	box-shadow: 0px 20px 40px rgba(15, 14, 69, 0.03);
	/* border-radius: 10px; */
	border-bottom-right-radius: 10px;
	border-bottom-left-radius: 10px;
	z-index: 12;
	overflow: hidden;
}
.select-list-form {
	left: 0;
	top: 112%;
	width: 100%;
}
.select-list-item,
.select-list-item-form {
	display: flex;
  align-items: center;
  justify-content: space-between;
  transition: .3s;
	height: 50px;
	font-size: 14px;
	font-weight: 600;
  padding: 3px 20px;
	z-index: 10;
}
.select-list-item-form {
	padding-left: 25px;
}
.select-list-item:hover {
	cursor: pointer;
  background: var(--logo-color);
  color: #fff;
}
.select-list-item-form:hover {
	background: #ededed;
	color: steelblue;
}
.fade-enter-active,
.fade-leave-active {
	transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
	opacity: 0;
}
.arrow-up {
  transform: rotate(180deg);
}
</style>