<template>
  <div>
    <div class="post-content-text" v-if="video.text">
      <p>{{ video.text }}</p>
    </div>
    <div class="post-video">
      <div class="pseudo-play" @click="pressPlay">
        <transition name="fade">
          <div class="pseudo-play-btn" v-if="play">
            <div class="arrow-play-vid" v-if="play"></div>
            <!-- <img :src="require('@/assets/images/svg/play_video.svg')" v-if="play" alt=""> -->
          </div>
        </transition>
      </div>
      <video :video="video" :src="video.video"></video>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PostVideo',
  props: ['video'],
  data() {
    return {
      play: true,
    }
  },
  methods: {
    pressPlay() {
      this.play = !this.play;
      if(!this.play) {
        this.$el.querySelector('video').play();
      }
      else
        this.$el.querySelector('video').pause();
    }
  }
}
</script>

<style>
.post-video {
  position: relative;
  display: flex;
  justify-content: center;
}
.post-video video {
  border: none;
  outline: none;
  border-radius: 10px;
  max-width: 100%;
}
.pseudo-play {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.pseudo-play-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  background: #fff;
  border-radius: 50%;
  transition: .3s;
}
.pseudo-play:hover {
  cursor: pointer;
}
.pseudo-play-btn:hover {
  background: #ededed;
}
.pseudo-play-btn:hover .arrow-play-vid {
  background: #000;
}
</style>