<script>
export default {
  name: 'PostPhotoPlus',
  props: ['photoPlus'],
  data() {
    return {
      popup: false,
    }
  },
  methods: {
    showPopup() {
      this.popup = true;
    },
    hidePopup() {
      this.popup = false;
    }
  }
}
</script>

<style>
.photo-plus-box {
  width: 100%;
  height: 100%;
}
.photo-plus {
  position: relative;
}
.folder-plus img,
.loupe-plus img,
.photo-plus img {
  /* position: relative; */
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}
.photo-panel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.loupe-plus {
  width: 18px;
  height: 18px;
}
.loupe-plus circle,
.loupe-plus line {
  transition: stroke .3s ease;
}
.pseudo-play-btn:hover circle {
  stroke: #000;
}
.pseudo-play-btn:hover line {
  stroke: #000;
}
.pseudo-play-btn:hover {
  cursor: pointer;
  background: #ededed;
}
.photo-plus-popup,
.loupe-plus:hover,
.folder-plus:hover {
  cursor: pointer;
}
.folder-plus {
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 45px;
}
.folder-plus path {
  transition: stroke .5s ease;
}
.folder-plus:hover path {
  stroke: var(--logo-color);
}
.photo-plus-popup,
.gallery-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  background: rgba(0, 0, 0, .7);
  /* border-radius: 10px; */
}
.photo-plus-popup img {
  max-width: 100%;
  max-height: 90%;
  object-fit: contain;
}
</style>