<template>
  <div class="slider-3d-container">
    <div class="arrow-left-3d" @click.stop="decrCurrentIndex">
      <svg width="25" height="40" xmlns="http://www.w3.org/2000/svg">
        <line x1="20" x2="3" y1="3" y2="22" stroke="#fff" stroke-width="5"/>
        <line x1="20" x2="3" y1="37" y2="18" stroke="#fff" stroke-width="5"/>
      </svg>
    </div>

    <div class="slider-3d-items-box">
      <div class="slider-3d-item" 
        v-for="(item, i) in slides" 
        :key="i" :style="itemStyle[i]">
        <img :src="currentPics[i]" alt="">
      </div>
    </div>

    <div class="arrow-right-3d" @click.stop="incrCurrentIndex">
      <svg width="25" height="40" xmlns="http://www.w3.org/2000/svg">
        <line x1="3" x2="20" y1="3" y2="22" stroke="#fff" stroke-width="5"/>
        <line x1="3" x2="20" y1="37" y2="18" stroke="#fff" stroke-width="5"/>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Slider3d',
  props: ['pics', 'sliderIndex'],
  data() {
    return {
      slides: [1,2,3,4,5],
      stylePattern: ['transform: translate3d(0%, 0, 0px);height:100%;top:0;z-index:10;',
        'transform: translate3d(10%, 0, -60px);height:90%;top:5%;z-index:9;', 
        'transform: translate3d(20%, 0, -120px);height:80%;top:10%;z-index:8;',
        'transform: translate3d(-20%, 0, -120px);height:80%;top:10%;z-index:8;',
        'transform: translate3d(-10%, 0, -60px);height:90%;top:5%;z-index:9;'
      ],
      itemStyle: [],
      currentPics: [],
      currentIndex: 0,
      indexCurrentPics: 2,
      count: 0,
    }
  },
  methods: {
    setStyles() {
      this.itemStyle = this.stylePattern.slice(this.currentIndex).concat(this.stylePattern.slice(0, this.currentIndex));
    },
    incrCurrentIndex() {
      // let indexPics = this.pics.length - 1 - this.count;
      // console.log('indexPics', indexPics);
      // this.count++;
      // if(this.count >= this.pics.length) {
      //   this.count = 0;
      // }
      this.count--;
      if(this.count < 0) {
        this.count = this.pics.length - 1;
      }
      // indexPics -    this.pics , 
      //     src   
      let indexPics = this.count - 2;
      if(indexPics < 0) {
        indexPics = this.pics.length - Math.abs(indexPics);
      }
      console.log('indexPics', indexPics);
      //     currentPics ( 5- ) 
      //      pics[indexPics]
      this.indexCurrentPics--;
      if(this.indexCurrentPics < 0) {
        this.indexCurrentPics = this.currentPics.length - 1;
      }
      console.log('indexCurrentPics', this.indexCurrentPics);
      // console.log('indexCurrentPics', this.indexCurrentPics)
      this.currentPics.splice(this.indexCurrentPics, 1, this.pics[indexPics]);

      let tmp = this.currentIndex;
      tmp++;
      if(tmp > this.slides.length - 1) {
        tmp = 0;
      }
      this.currentIndex = tmp;
      this.setStyles();
      this.$emit('getCount', this.count);
    },
    decrCurrentIndex() {
      this.count++;
      if(this.count >= this.pics.length) {
        this.count = 0;
      }
      // indexPics -    this.pics , 
      //     src   
      let indexPics = this.count + 2;
      if(indexPics >= this.pics.length) {
        indexPics = indexPics - this.pics.length;
      }
      console.log('indexPics', indexPics);
      //     currentPics ( 5- ) 
      //      pics[indexPics]
      this.indexCurrentPics++;
      if(this.indexCurrentPics >= this.currentPics.length) {
        this.indexCurrentPics = 0;
      }
      console.log('indexCurrentPics', this.indexCurrentPics);
      this.currentPics.splice(this.indexCurrentPics, 1, this.pics[indexPics]);
      
      let tmp = this.currentIndex;
      tmp--;
      if(tmp < 0) {
        tmp = this.slides.length - 1;
      }
      this.currentIndex = tmp;
      this.setStyles();
      this.$emit('getCount', this.count);
    },
    // sendCount() {
    //   this.$emit('getCount', this.count);
    // }
  },
  computed: {},
  created() {
    this.currentPics = this.pics.slice(0, 3).concat(this.pics.slice(-2));
    this.itemStyle = this.stylePattern;
  },
  mounted() {
    if(this.sliderIndex > 2) {
      for(let i = 0; i < this.sliderIndex; i++) {
        this.decrCurrentIndex();
      }
    }
  }
}
</script>

<style>
.slider-3d-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  user-select: none;
}
.slider-3d-items-box {
  position: relative;
  perspective: 1200px;
  transform-style: preserve-3d;
  width: 63%;
  height: 63vh;
  
}
.slider-3d-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* border: 1px solid silver; */
  border-radius: 10px;
  background: #000;
  transform-style: preserve-3d;
  will-change: transform;
  transition: .5s ease-in-out;
}
.slider-3d-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}
.arrow-left-3d:hover,
.arrow-right-3d:hover {
  cursor: pointer;
}
.arrow-left-3d:hover line {
  stroke: #1f3efa;
}
.arrow-right-3d:hover line {
  stroke: #1f3efa;
}
</style>