<template>
  <div class="form-wrapper">
    <div class="form-side">
      <div class="form-side-item" 
        :class="{'form-side-item-active' : !isRegistered}" 
        @click="$store.commit('setRegistration')"
      >
        <div class="form-side-item-img">
          <img :src="require('@/assets/images/svg/key.svg')" alt="">
        </div>
      </div>
      <div class="form-side-item" 
        :class="{'form-side-item-active' : isRegistered}" 
        @click="$store.commit('setLogin')"
      >
        <div class="form-side-item-img">
          <img :src="require('@/assets/images/svg/one_plus.svg')" alt="">
        </div>
      </div>
    </div>
    <div class="form-main">
      <div class="form-box" v-if="!isRegistered">
        <form action="#">
          <div class="form-purpose">Join community</div>
          <div class="form-input mb10">
            <label>
              <input type="text" name="" id="">
              <div class="form-input-placeholder">
                <span>First Name</span>
              </div>
            </label>
          </div>
          <div class="form-input mb10">
            <label>
              <input type="text" name="" id="">
              <div class="form-input-placeholder">
                <span>Last Name</span>
              </div>
            </label>
          </div>
          <div class="form-input">
            <label>
              <input type="email" name="" id="">
              <div class="form-input-placeholder">
                <span>Your Email</span>
              </div>
            </label>
          </div>
          <div class="component-box">
            <Select :selectData="selectForm" style="margin-left:0;margin-right:0;" :hover="true" />
          </div>
          <div class="form-accept-terms">
            <div class="form-accept-terms-check" 
            @click="$store.commit('acceptPrivacyPolicy')">
              <img 
                :src="require('@/assets/images/svg/check_mark.svg')" 
                v-if="privacyPolicy" 
                alt=""
              >
            </div>
            <div class="form-accept-terms-text">
              <span>I accept the</span>
              <div class="privacy-policy"> Terms of use</div>
              <span>and read</span>
              <div class="privacy-policy">Privacy Policy</div>
            </div>
          </div>
          <div class="form-submit">
            <label for="">
              <input type="submit" value="">
              <span>Complete Registration!</span>
            </label>
          </div>
        </form>
      </div>
      <div class="form-box" v-if="isRegistered">
        <form action="#">
          <div class="form-purpose">Login to your account</div>
          <div class="form-input">
            <label>
              <input type="email" name="" id="">
              <div class="form-input-placeholder">
                <span>Your Email</span>
              </div>
            </label>
          </div>
          <div class="form-remember-me">
            <input type="checkbox" name="" id="form-login-remember">
            <label for="form-login-remember"><span>Remember me</span></label>
          </div>
          <div class="form-submit">
            <button>Login</button>
            <!-- <label for="">
              <input type="submit" value="">
              <span>Login</span>
            </label> -->
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { mapMutations } from 'vuex';
import Select from '@/components/Select.vue'

export default {
  name: 'Form',
  components: {
    Select,
  },
  computed: {
    ...mapState([
      'isRegistered',
      'selectForm',
      'privacyPolicy'
    ])
  },
  methods: {
    ...mapMutations([
      'setRegistration',
      'setLogin',
      'acceptPrivacyPolicy',
    ])
  }
}
</script>

<style>
.form-wrapper {
  display: flex;
  background: var(--logo-color);
  border-radius: 10px;
  user-select: none;
}
.form-side {
  width: 70px;
  min-width: 70px;
}
.form-side-item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50%;
  transition: .5s;
}
.form-side-item:hover {
  cursor: pointer;
}
.form-side-item:first-child {
  border-top-left-radius: 10px;
}
.form-side-item:last-child {
  border-bottom-left-radius: 10px;
}
.form-side-item-active {
  background: #0E2BDD;
}
.form-side-item-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}
.form-main {
  flex: 1;
}
.form-box {
  margin: var(--margin-dt);
}
.form-purpose {
  color: #fff;
  margin-bottom: var(--margin-dt);
}
.form-input,
.component-box {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  background: var(--logo-color);
  border-radius: 10px;
  border: 1px solid #fff;
  margin-right: 32px;
}
.component-box {
  border: none;
}
.form-input input {
	border: none;
	outline: none;
	padding-left: 25px;
	background: var(--logo-color);
  color: #fff;
  font-size: 14px;
	-webkit-box-shadow: inset 0 0 0 50px var(--logo-color); 
	box-shadow: inset 0 0 0 50px var(--logo-color);
}
.form-input-placeholder {
  position: absolute;
  left: 10px;
  top: 15px;
  display: flex;
  align-items: center;
  padding-left: 14px;
  z-index: 1;
  font-size: 14px;
  color: #fff;
  background: var(--logo-color);
  width: 90%;
  height: 30px;
}
.form-input-placeholder:hover {
  cursor: text;
}
.form-input input:focus + .form-input-placeholder {
  display: none;
}
.form-remember-me {
  position: relative;
  margin: 18px 0;
}
.form-remember-me label {
  position: relative;
  padding-left: 30px;
  color: #fff;
  font-size: 14px;
}
.form-remember-me label::before {
  position: absolute;
  top: -2px;
  left: 0;
  content: '';
  width: 18px;
  height: 18px;
  border: 1px solid#fff;
  border-radius: 5px;
}
.form-remember-me label::after {
  content: url(data:image/svg+xml;base64,);
  width: 18px;
  height: 18px;
  position: absolute;
  top: 0px;
  left: 2px;
  opacity: 0;
  transition: .5s;
}
.form-remember-me input:checked + label::after {
  opacity: 1;
}
.form-remember-me label:hover {
  cursor: pointer;
}
.form-remember-me input {
  position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;
}
.form-submit {
  height: 60px;
  background: #fff;
  border-radius: 10px;
  border: 1px solid #fff;
  font-size: 14px;
  font-weight: 600;
}
.form-submit button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 700;
  border: none;
  outline: none;
}
.form-submit button:hover {
  cursor: pointer;
}
/*
.form-submit label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.form-submit label:hover {
  cursor: pointer;
}
.form-submit input {
  height: 0;
  border: none;
  outline: none;
}
*/
.mb10 {
  margin-bottom: 10px;
}
.form-select {
  background: var(--logo-color);
  border: 1px solid #fff;
  width: 100%;
  margin: 10px 0 0 0;
}
.form-accept-terms {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 18px 0;
}
.form-accept-terms-check:hover {
  cursor: pointer;
}
.form-accept-terms-check {
  width: 18px;
  height: 18px;
  border: 1px solid#fff;
  border-radius: 5px;
}
.form-accept-terms-text {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  font-size: 10px;
  color: #fff;
}
.privacy-policy {
  font-size: 12px;
  margin: 0 5px;
}
.privacy-policy:last-child {
  margin-right: 0;
}
.privacy-policy:hover {
  cursor: pointer;
}
@media (max-width: 500px) {
  .component-box {
    margin-top: 5px;
  }
  .form-wrapper {
    flex-direction: column;
    width: 100%;
  }
  .form-side {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 70px;
  }
  .form-side-item {
    flex: 1;
    height: 100%;
  }
  .form-side-item:last-child {
    border-bottom-left-radius: 0;
    border-top-right-radius: 10px;
  }
  .form-main {
    display: flex;
    justify-content: center;
  }
  .form-accept-terms {
    margin-right: 0;
  }
}
</style>