<template>
  <div class="post-audio">
    <div class="post-audio-top">
      <div class="post-audio-pic">
        <img :src="postAudio.img" alt="">
      </div>
    </div>
    <div class="post-audio-bottom">
      <PlayListItem 
        v-for="(track, index) in postAudio.tracks" 
        :key="track.id" 
        :trackInfo="track" 
        :isPlaying="currentIndex == index" 
        @playMe="toggleIndex(index)" 
        :duration="lenTracks[index]" 
        :curLen="curTimeTracks[index]" 
        :ended="isEnded" 
        :index="index" 
        :curI="currentIndex"
        @changeCurrentTime="changeHandleCurrentTime" 
      />
    </div>
    
  </div>
</template>

<script>
import PlayListItem from '@/components/PlayListItem.vue'

export default {
  name: 'PostAudio',
  props: ['postAudio'],
  components: {
    PlayListItem,
  },
  data() {
    return {
      currentIndex: 0,
      currentTime: 0,
      audio: new Audio(),
      isPlay: false,
      width: 0,
      duration: 0,
      curTimeTracks: [],
      lenTracks: [],
      isEnded: false,
    }
  },
  methods: {
    toggleIndex(i) {
      this.setCurTime();
      //        
      if(i == this.currentIndex) {
        // console.log('   ', this.isPlay);
        //    
        if(this.isPlay ) {
          this.audio.pause();
          this.isPlay = false;
          this.isEnded = false;
        }
        // ,   play
        else {
          this.audio.play();
          this.isPlay = true;
          this.isEnded = true;
        }
      }
      //    ,  
      else {
        // console.log(' ', this.isPlay);
        this.currentIndex = i;
        this.audio.src = this.postAudio.tracks[i].url;
        this.audio.addEventListener('canplay', this.playTrack);
        this.audio.addEventListener('timeupdate', this.setCurTime);

      }
    },
    setParams() {
      this.duration = Math.round(this.audio.duration);
      this.lenTracks.splice(this.currentIndex, 1, Math.round(this.audio.duration))
    },
    playTrack() {
      this.audio.play();
      this.duration = Math.round(this.audio.duration);
      this.isPlay = true;
      this.isEnded = true;
    },
    setCurTime() {
      this.currentTime = Math.round(this.audio.currentTime);
      this.curTimeTracks.splice(this.currentIndex, 1, Math.round(this.audio.currentTime))
    },
    getPercent(len, num) {
      return num / (len / 100);
    },
    setCurLen() {
      this.lenTracks.splice(this.currentIndex, 1, Math.round(this.audio.duration));
      this.currentIndex++;
      if(this.currentIndex < this.postAudio.tracks.length) {
        this.setLengthTracks();
      }
    },
    setLengthTracks() {
      this.audio.src = this.postAudio.tracks[this.currentIndex].url;
      this.audio.addEventListener('loadedmetadata', this.setCurLen, {once: true});
    },
    formatTime(durationSec) {
      let res = '';
      let sec = durationSec > 60 ? durationSec % 60 : durationSec;
      if(+sec < 10) sec = '0' + sec;
      let minut = durationSec > 60 ? Math.trunc(durationSec / 60) : '0';
      if(+minut < 10) minut = '0' + minut;
      let hour = durationSec > 3600 ? Math.trunc(durationSec / 3600) : '0';
      res = hour + ':' + minut + ':' + sec;
      return res;
    },
    async loadDurations() {
      await this.postAudio.tracks.forEach((track, i) => {
        const audio = new Audio();
        audio.src = track.url;
        audio.addEventListener(
          "loadedmetadata",
          () => {
            this.lenTracks.splice(i, 1, Math.round(audio.duration));
            this.duration = Math.round(audio.duration);
          },
          { once: true }
        );
      });
      
      this.currentIndex = 0;
      this.audio.src = this.postAudio.tracks[0].url;
    },
    changeHandleCurrentTime() {
      this.audio.currentTime = this.$store.state.audioHandleTime;
    }
  },
  computed: {
    
  },
  // watch: {
  //   tracks: {
  //     immediate: true,
  //     handler: function () {
  //       this.loadDurations();
  //     },
  //   },
  // },
  mounted() {
    this.audio.addEventListener('timeupdate', () => {
      if (this.audio.duration)
        this.setCurTime();
    } );
    this.audio.addEventListener('ended', () => this.isEnded = false);
    this.loadDurations();
  },
  created() {
    this.curTimeTracks = this.postAudio.tracks.slice();
    for(let i = 0; i < this.curTimeTracks.length; i++) {
      this.curTimeTracks[i] = 0;
    }
    this.lenTracks = this.postAudio.tracks.slice();
  }
}
</script>

<style>
.post-audio {
  position: relative;
  min-height: 200px;
}
.post-audio-top {
  border-radius: 10px;
}
.post-audio-top-over {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 17.03%, #FFFFFF 96.56%);
}
.post-audio-pic img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}
.post-audio-bottom {
  position: relative;
  margin-top: -72px;
  padding-top: 20px;
  background: linear-gradient(rgba(255, 255, 255, 0) 10px, rgba(255, 255, 255, .5) 35px, rgba(255, 255, 255, 1) 54px );
  
}
.post-audio-item {
  width: 100%;
}
.audio-custom {
  display: flex;
  align-items: center;
  margin: 10px var(--margin-progress);
}
.audio-custom-btn,
.audio-custom-btn-white {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: var(--logo-color);
  transition: .3s;
}
.audio-custom-btn-white {
  background: #fff;
}
.audio-custom-btn-white:hover {
  background: #ededed;
  cursor: pointer;
}
.audio-custom-btn-white:hover .arrow-play {
  background: #000;
}
.audio-custom-menu,
.audio-custom-btn:hover {
  cursor: pointer;
}
.audio-custom-btn:hover {
  background: #000;
}
.audio-custom-btn-pl {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
}
.arrow-play,
.arrow-play-vid,
.arrow-play-white {
  width: 16px;
  height: 16px;
  clip-path: polygon(30% 5%, 100% 50%, 30% 95%);
  transition: .3s;
}
.arrow-play-vid,
.arrow-play {
  background: var(--logo-color);
}
.arrow-play-white {
  background: #fff;
}
.audio-custom-btn:hover .arrow-play {
  background: #000;
}
.audio-custom-btn-pl img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.audio-custom-info {
  flex: 1;
  margin-left: 20px;
}
.audio-custom-singer {
  font-size: 12px;
  font-weight: 500;
  color: var(--primary-gray);
}
.audio-custom-song-name {
  font-size: 14px;
  font-weight: 600;
}
.audio-custom-song-time-box {
  display: flex;
}
.song-time-after,
.song-time-before {
  font-size: 10px;
  font-weight: 600;
  margin: 0 2px;
}
.audio-custom-menu {
  margin-left: 15px;
}
.audio-progress-bar {
  border-top: 2px solid var(--logo-color);
  margin: var(--margin-progress);
}
.absolute-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
}
.cb {
  color: #000;
}

</style>