<template>
  <div class="post-vote">
    <div class="post-text">
      <div class="post-content-text">
        <p>{{ postVote.text }}</p>
      </div>
    </div>
    <div class="post-vote-main" v-if="!result">
      <div class="post-vote-btn" 
        v-for="(btn, index) in postVote.vote" 
        :key="index"
        @click="setVote(index)"
        :class="{ 'is-vouted': votes[index] }"
      >
        <div class="post-vote-btn-title">
          {{ btn }}
        </div>
      </div>
      <div class="post-vote-footer">Vote to see results</div>
    </div>
<!-- @click="setVote(index)" str.26 -->
    <div class="post-result-main" v-if="result">
      <div class="post-result-btn" 
        v-for="(btn, index) in postVote.vote" 
        :key="index" 
        
      >
        <div class="post-result-btn-left" 
          :style="'width:' + percents[index] + '%'"
          :class="{ 'post-result-active': votes[index] }"
        >
          <div class="post-result-btn-title">
            {{ btn }}
          </div>
        </div>
        <div class="post-result-btn-profiles">
          <div class="post-result-btn-ava-box">
            <div 
              class="post-result-btn-ava" 
              v-for="(ava, i) in postVote.avatars"
              :key="i"
            >
              <img :src="ava" alt="">
            </div>
          </div>
          <div class="post-result-btn-percent"> {{ postVote.percents[index] }}%  </div>
        </div>
      </div>
      <div class="post-vote-footer">{{ postVote.voted }} voted</div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'PostVote',
  props: ['postVote'],
  data() {
    return {
      votes: [],
      isV: 0,
      result: false,
      percents: [0,0,0],
    }
  },
  computed: {
    getVote() {
      return this.votes[this.isV];
    }
  },
  methods: {
    setVote(index) {
      this.votes = this.votes.map((el, i) => el = i == index);
      this.result = true;
      setTimeout(() => {this.percents = this.postVote.percents}, 500);
      // this.percents = this.postVote.percents;
    },
  },
  mounted() {
    this.votes = new Array(this.postVote.vote.length).fill(false);
  }
}
</script>

<style>
.post-vote-btn {
  display: flex;
  align-items: center;
  height: 50px;
  border: 1px solid var(--primary-gray);
  border-radius: 10px;
  margin-bottom: 10px;
}
.post-vote-btn:hover {
  cursor: pointer;
}
.post-vote-btn-title {
  font-size: 14px;
  font-weight: 500;
  margin-left: 20px;
}
.post-vote-footer {
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  color: var(--primary-gray);
}
.is-vouted {
  border: 1px solid var(--logo-color);
}
.post-result-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  border-radius: 10px;
  margin-bottom: 10px;
  background: rgba(31, 62, 250, 0.1);
}
.post-result-btn {
  cursor: pointer;
}
.post-result-btn-left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  background: rgba(165, 180, 203, 0.5);
  border-radius: 10px;
  transition: width .5s;
}
.post-result-btn-title {
  font-size: 14px;
  font-weight: 500;
  margin-left: 20px;
  white-space: nowrap;
}
.post-result-btn-profiles {
  display: flex;
  align-items: center;
  height: 20px;
  margin-right: 15px;
}
.post-result-btn-ava-box {
  display: flex;
  margin-right: 15px;
}
.post-result-btn-ava {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #fff;
  overflow: hidden;
  margin-left: -6px;
}
.post-result-btn-ava img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.post-result-btn-percent {
  font-size: 12px;
  font-weight: 700;
  width: 25px;
  text-align: end;
}
.post-result-active {
  background: var(--logo-color);
  color: #fff;
}
</style>