<template>
	<div class="select-wrapper">
		<div class="select-header" @mouseleave="showMatches = false">
			<div class="select-header-left">
				<img :src="require('@/assets/images/svg/tags.svg')" alt="" />
			</div>
			<div class="header-tags">
				<input type="text" value="" v-model.trim="input" @input="findMatches" />
				<!-- <transition name="fade">
					<div class="header-tags-search" v-if="showMatches">
						<div
							class="header-tags-search-item"
							v-for="(tag, index) in matches"
							:key="index"
              @click="setMatch(tag)"
						>
              <div class="header-tags-search-item-name">{{ tag }}</div>
              <div class="header-tags-search-item-num">{{ ++index * 10000 }}</div>
            </div>
					</div>
				</transition> -->
			</div>
      <transition name="fade">
        <div class="header-tags-search" v-if="showMatches">
          <div
            class="header-tags-search-item"
            v-for="(tag, index) in matches"
            :key="index"
            @click="setMatch(tag)"
          >
            <div class="header-tags-search-item-name">{{ tag }}</div>
            <div class="header-tags-search-item-num">{{ ++index * 10000 }}</div>
          </div>
        </div>
      </transition>
		</div>
	</div>
</template>

<script>
import { mapState } from "vuex";

export default {
	name: "Tags",
  data() {
    return {
      input: '',
      matches: [],
      showMatches: false,
    }
  },
	computed: {
		...mapState(["tags"]),
	},
  methods: {
    findMatches() {
      let reg = new RegExp(this.input);
      this.matches = this.tags.filter(el => reg.test(el));
      this.showMatches = !!this.matches.length;
    },
    setMatch(name) {
      this.input = name;
      this.showMatches = false;
    }
  }
};
</script>

<style>
.header-tags {
  position: relative;
	flex: 1;
}
.header-tags input {
  width: 100%;
	border: none;
	outline: none;
	padding-left: 10px;
	background: #fff;
	-webkit-box-shadow: inset 0 0 0 50px #fff;
	box-shadow: inset 0 0 0 50px #fff;
}
.header-tags-search {
  position: absolute;
  top: 81%;
  left: 0;
  width: 100%;
  background: #FFFFFF;
	box-shadow: 0px 20px 40px rgba(15, 14, 69, 0.03);
	/* border-radius: 10px; */
  border-bottom-right-radius: 10px;
	border-bottom-left-radius: 10px;
	max-height: 150px;
	overflow-y: auto;
  z-index: 10;
}
.header-tags-search-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  transition: .3s;
  padding: 3px 20px;
}
.header-tags-search-item:hover {
  cursor: pointer;
  background: var(--logo-color);
  color: #fff;
}
.header-tags-search-item-name {
  font-size: 12px;
}
.header-tags-search-item-num {
  font-size: 12px;
  color: var(--primary-gray);
  /* margin-right: 5px; */
}
::-webkit-scrollbar {
  width: 7px;
  background-color: #dcdcdc;
  border-radius: 3px;
}
::-webkit-scrollbar-thumb {
    background-color: #778899;
    border-radius: 3px;
}
</style>