<template>
  <div class="post-audio-item">
    <div class="audio-custom">
      <div class="audio-custom-btn-white" @click.stop="playMe" v-if="index == 0">
        <div class="audio-custom-btn-pl " >
          <div class="arrow-play"></div>
          <img v-if="isPlaying && ended" :src="require('@/assets/images/svg/pause.svg')" alt="">
        </div>
      </div>
      <div class="audio-custom-btn" @click.stop="playMe" v-if="index != 0">
        <div class="audio-custom-btn-pl">
          <div class="arrow-play-white"></div>
          <!-- <img :src="require('@/assets/images/svg/play_audio.svg')" v-if="!songsIn[index]" alt=""> -->
          <img v-if="isPlaying && ended" :src="require('@/assets/images/svg/pause_white.svg')"  alt="">
        </div>
      </div>
      <div class="audio-custom-info">
        <div class="audio-custom-singer" :class="{ cb: index == 0 }">{{ trackInfo.artist }}</div>
        <div class="audio-custom-song-name">{{ trackInfo.name }}</div>
      </div>
      <div class="audio-custom-song-time-box" :class="{ 'not-play': !isPlaying }">
        <div class="song-time-before">{{ formatTime(curLen) }}</div>
        <div class="song-time-after">/{{ formatTime(duration)  }}</div>
      </div>
      <div class="audio-custom-menu" :class="{ 'not-play': !isPlaying }">
        <svg width="6" height="18" xmlns="http://www.w3.org/2000/svg">
          <circle r="1" cx="3" cy="3" stroke="#000"/>
          <circle r="1" cx="3" cy="9" stroke="#000"/>
          <circle r="1" cx="3" cy="15" stroke="#000"/>
        </svg>
        <!-- <img :src="require('@/assets/images/svg/audio_menu.svg')" alt=""> -->
      </div>
    </div>
    <div class="audio-item-progress-box" 
      :class="{ h10: isPlaying || startFlag }" 
      @click.stop="setCurrentTime" 
      ref="progress"
    >
      <div class="audio-item-progress-cur" :style="{width: getPercent + '%'}"></div>
    </div>
  </div>
</template>

<script>

export default {
  name: "PlaylistItem",
  props: [
    'trackInfo', 
    'isPlaying', 
    'curLen', 
    'duration', 
    'ended', 
    'index',
    'curI'
  ],
  data() {
    return {
      startPosition: true,
    };
  },
  methods: {
    playMe() {
      this.startPosition = false;
      this.$emit('playMe');
    },
    formatTime(durationSec) {
      let res = '';
      let sec = durationSec > 60 ? durationSec % 60 : durationSec;
      if(+sec < 10) sec = '0' + sec;
      if(!sec) sec = '00';
      let minut = durationSec > 60 ? Math.trunc(durationSec / 60) : '0';
      if(+minut < 10) minut = '0' + minut;
      let hour = durationSec > 3600 ? Math.trunc(durationSec / 3600) : '0';
      res = hour + ':' + minut + ':' + sec;
      return res;
    },
    setCurrentTime(e) {
      let progress = this.$refs.progress;
      let metrix = progress.getBoundingClientRect();
      let progressLeft = e.clientX - metrix.left;
      let percent = metrix.width / 100;
      let shiftLeft = progressLeft / percent;
      let sec = Math.round(this.duration / 100 * shiftLeft);
      this.$store.commit('setAudioHandleTime', sec);
      this.$emit('changeCurrentTime');
    }
  },
  computed: {
    startFlag() {
      return this.startPosition && this.curI == 0 && this.index == 0;
    },
    getPercent() {
      return this.curLen / (this.duration / 100);
    },
  },
  mounted() {
  }
};
</script>

<style>
.audio-item-progress-box {
  position: relative;
  background: #dcdcdc;
  height: 0;
  margin: 0 var(--margin-progress);
  border-radius: 5px;
  overflow: hidden;
  transition: height .5s;
}
.audio-item-progress-box:hover {
  cursor: pointer;
}
.audio-item-progress-cur {
  height: 100%;
  background: var(--logo-color);
  border-radius: 5px;
  /* transition: 1s ease-in; */
}
.not-play {
  color: var(--primary-gray);
}
.not-play circle {
  stroke: var(--primary-gray);
}
.h10 {
  height: 10px;
}
</style>
