<template>
  <div class="post-gallery">
    <div class="post-text">
      <div class="post-content-text">
        <p>{{ postGallery.text }}</p>
      </div>
    </div>
    <div class="post-gallery-box">
      <div class="post-gallery-box-top">
        <div class="post-gallery-box-top-item" 
          v-for="(item, index) in getTop" 
          :key="index" 
          @click="getSliderIndex(item)" 
        >
          <img :src="item" alt="">
        </div>
      </div>
      <div class="post-gallery-box-bottom">
        <div class="post-gallery-box-bottom-item" 
          v-for="(pic, i) in getBottom" 
          :key="i" 
          @click="getSliderIndex(pic)" 
        >
          <img :src="pic" alt="">
        </div>
      </div>
    </div>
    <transition name="fade">
      <!-- <div class="gallery-popup" @click="hidePopup"> -->
      <div class="gallery-popup" v-if="popup" @click="hidePopup">
        <div class="wrapper-slider-folders" >
          <div class="gallery-top-box">
            <div class="gallery-top-item">
              <div class="gallery-top-item-title"> {{ postGallery.title }} </div>
              <div class="gallery-top-item-count"> Photo
                <span> {{ currentPic }} </span> of 
                <span> {{ pics.length }}</span> 
              </div>
            </div>
          </div>
          <Slider3d :pics="pics" :sliderIndex="sliderIndex" @getCount="setCount" />
          <div class="gallery-folders">
            <div class="gallery-folders-box">
