                  <text x="30" y="30" class="folder-title" >89</text>
                </svg>
                <div class="folder-bottom">Album Name</div>
              </div>
              <div class="gallery-folders-item">
                <svg width="60" height="48" viewBox="0 0 60 48" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M24.1666 0.666672H6.66657C3.44365 0.666672 0.862403 3.27709 0.862403 6.50001L0.833237 41.5C0.833237 44.7229 3.44365 47.3333 6.66657 47.3333H53.3332C56.5562 47.3333 59.1666 44.7229 59.1666 41.5V12.3333C59.1666 9.11042 56.5562 6.50001 53.3332 6.50001H29.9999L24.1666 0.666672Z" fill="#1F3EFA"/>
                  <text x="30" y="30" class="folder-title" >89</text>
                </svg>
                <div class="folder-bottom">Album Name</div>
              </div>
              <div class="gallery-folders-item">
                <svg width="60" height="48" viewBox="0 0 60 48" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M24.1666 0.666672H6.66657C3.44365 0.666672 0.862403 3.27709 0.862403 6.50001L0.833237 41.5C0.833237 44.7229 3.44365 47.3333 6.66657 47.3333H53.3332C56.5562 47.3333 59.1666 44.7229 59.1666 41.5V12.3333C59.1666 9.11042 56.5562 6.50001 53.3332 6.50001H29.9999L24.1666 0.666672Z" fill="#1F3EFA"/>
                  <text x="30" y="30" class="folder-title" >89</text>
                </svg>
                <div class="folder-bottom">Album Name</div>
              </div>
              <div class="gallery-folders-item">
                <svg width="60" height="48" viewBox="0 0 60 48" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M24.1666 0.666672H6.66657C3.44365 0.666672 0.862403 3.27709 0.862403 6.50001L0.833237 41.5C0.833237 44.7229 3.44365 47.3333 6.66657 47.3333H53.3332C56.5562 47.3333 59.1666 44.7229 59.1666 41.5V12.3333C59.1666 9.11042 56.5562 6.50001 53.3332 6.50001H29.9999L24.1666 0.666672Z" fill="#fff"/>
                  <text x="30" y="30" class="folder-title create-folder" >+</text>
                </svg>
                <div class="folder-bottom">Create album</div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Slider3d from '@/components/Slider3d.vue'

export default {
  name: 'PostGallery',
  props: ['postGallery'],
  components: {
    Slider3d
  },
  data() {
    return {
      sliderIndex: 2,
      popup: false,
      currentPic: 0,
      pics: [
        'https://funik.ru/wp-content/uploads/2019/03/7ba1b80270e37300e466.jpg',
        'https://s1.1zoom.ru/big3/938/335876-SoLoVuShKa.jpg',
        'https://wallpaper-mania.com/wp-content/uploads/2018/09/High_resolution_wallpaper_background_ID_77701566278.jpg',
        'https://img.freepik.com/free-photo/men-checking-stock-pastry-bakery-shop_53876-15918.jpg?size=626&ext=jpg',
        'https://img5.goodfon.ru/original/1920x1200/1/bf/krater-gory-ozero.jpg',
        'https://wikiq.ru/wp-content/uploads/2018/03/top-10-samyh-dorogih-mashin-i-bugatti.jpg',
        'https://ocean-media.su/wp-content/uploads/2015/07/Hvitzerkur-chto-e-to-za-chudo-prirody--e1436434844826.jpg',
        'https://spectrum.ru/images/dynamic/img55289.jpg',
        'https://s-cdn.sportbox.ru/images/styles/upload/fp_fotos/eb/e7/IMG_4007_1.jpg',
        'https://img1.goodfon.ru/wallpaper/nbig/a/ad/007-007-spectre-james-bond.jpg',
        
      ],
    }
  },
  computed: {
    getTop() {
      return this.postGallery.gallery.filter((el, i) => i < 2);
    },
    getBottom() {
      return this.postGallery.gallery.filter((el, i) => i > 1);
    },
    
  },
  methods: {
    getSliderIndex(item) {
      let index = this.pics.indexOf(item, 0);
      // console.log(index);
      this.sliderIndex = index;
      if(!index)
        this.sliderData = this.postGallery.gallery;
      else {
        this.sliderData = this.postGallery.gallery.slice(index).concat(this.postGallery.gallery.slice(0, index))
      }
      this.showPopup();
      // console.log(this.popup)
    },
    showPopup() {
      this.popup = true;
    },
    hidePopup() {
      this.popup = false;
    },
    setCount(value) {
      // console.log('value', value);
      let tmp = value;
      // if(tmp <= 0)
      //   tmp = this.pics.length - Math.abs(tmp);
      this.currentPic = tmp + 1;
    }
  }
}
</script>

