<template>
  <div class="header-bottom">
    <div class="header-bottom-item header-bottom-left">
      <Select :selectData="item" v-for="item in getLeft" :key="item.id" />
    </div>
    <div class="header-bottom-item">
      <Select :selectData="selects[2]" />
      <Tags />
    </div>
  </div>
</template>

<script>
import Select from '@/components/Select.vue'
import Tags from '@/components/Tags.vue'
import { mapState } from 'vuex'

export default {
  name: 'HeaderBottom',
  components: {
    Select,
    Tags,
  },
  computed: {
    ...mapState([
      'selects',
    ]),
    getLeft() {
      let arr = [];
      for(let i = 0; i < 2; i++) {
        arr[i] = this.selects[i];
      }
      return arr;
    }
  },
}
</script>

<style>
.header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: var(--margin-dt);
}
.header-bottom-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
}
.header-bottom-left {
  margin-right: var(--margin-dt);
}
@media (max-width: 991px) {
  .header-bottom {
    flex-direction: column;
    align-items: center;
  }
  .header-bottom-item {
    width: 100%;
    margin: 0 0 var(--margin-dt) 0;
  }
  .header-bottom-item:last-child {
    margin: 0;
  }
}
@media (max-width: 576px) {
  .header-bottom-item {
    flex-direction: column;
    margin: 0;
  }
  .select-wrapper,
  .select-wrapper:last-child {
    margin: var(--margin-dt);
    width: 100%;
    margin-top: 0;
  }
}
</style>